@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Prompt:wght@500;600&display=swap");

.header {
  width: 100%;
}

.topNavBar {
  display: flex;
  width: 95%;
  margin: 0 auto;
  justify-content: space-between;
  border-bottom: 1px solid rgb(189, 174, 174);
}

.searchSection {
  display: flex;
  width: 90%;
  margin: 0 auto;
  padding: 3vh 0;
  justify-content: space-between;
  align-items: center;
}

.companyNameSection {
  width: 20%;
  font-size: 2em;
  font-family: "Prompt", sans-serif;
  font-weight: 600;
}

.iconsSection {
  display: flex;
  width: 10%;
}

.icon {
  width: 2vw;
  padding: 1vh 0 0 2vw;
}
