@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  width: 100%;
  font-size: 0.8rem;
  color: #151515;
  background-color: #fdfdfd;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h2 {
  font-size: 4em;
  text-align: center;
  padding: 4vh 0;
}
